.team{
    padding-top: 50px;
    padding-bottom: 50px;
}

.team .main-title{
    text-align: center;
    color: var(--color-primary);
    margin-bottom: 20px;
}

.team .parag{
    color: var(--color-bg);
    text-align: center;
    margin-bottom: 30px;
}

.team .container{
    display: grid;
    grid-template-columns: repeat(auto-fit , minmax(250px , 1fr));
    grid-gap: 40px;
}

.team .container .box-item{
    padding:30px;
    text-align: center;
    border-radius: 6px;
    box-shadow: 0px 1px 10px 7px rgb(170 170 170 / 20%);
    -webkit-box-shadow: 0px 1px 10px 7px rgb(170 170 170 / 20%);
    -moz-box-shadow: 0px 1px 10px 7px rgb(170 170 170 / 20%);
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-radius: 6px;
}

.team .container .box-item .text {
    height: 100px;
}
.team .container .box-item .text h2{
    margin-top: 10px;
    color: var(--color-primary);
}

.team .container .box-item .text p{
    margin-top: 10px;
    color: var(--color-bg);
    margin-bottom: 20px;
}

.team .container .box-item a{
    margin: 0 20px;
    font-size: 20px;
}

.team img{
    width: 100%;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

