.video-section {
  height: 100vh;
  overflow: hidden;
}

video {
  position: absolute;
  object-fit: cover;
  right: 0;
  top: 0;
  min-width: 100%;
  min-height: 100%;
  z-index: -1;
  margin: 0 auto;
}

.video-section .video-content p {
  color: var(--color-light);
}

@media (max-width: 767px) {
  video {
    width: auto;
    height: auto;
  }
}

.video-section .video-content {
  position: relative;
  top: 50%;
  left: 12%;
}
.video-section .video-content h2 {
  margin-bottom: 10px;
  font-size: 40px;
  color: var(--color-white);
}
.video-section .video-content p {
  border-color: var(--color-primary);
  margin-bottom: 15px;
}

@media (max-width: 767px) {
  .video-section .video-content {
    margin-right: 0 auto;
  }
  .video-section .video-content h2 {
    font-size: 19px;
    margin-bottom: 15px;
  }
  .video-section .video-content p {
    width: 100%;
    font-size: 12px;
  }
}

.we-are {
  background-color: var(--color-white);
  padding-top: 80px;
  padding-bottom: 80px;
}

.we-are .content-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.we-are .content-box span {
  margin-bottom: 10px;
  color: var(--color-bg);
}
.we-are .content-box h1 {
  margin-bottom: 10px;
  color: var(--color-primary);
}
.we-are .content-box p {
  margin-bottom: 10px;
  color: var(--color-bg);
  text-align: center;
}
/* Float three column-boxs side by side */
.column-box {
  float: left;
  width: 30%;
  padding: 1.5%;
}

.row-side {
  padding-top: 20px;
  margin: 0 -5px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

/* Clear floats after the column-boxs */
.row-side:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive column-boxs */
@media screen and (max-width: 767px) {
  .column-box {
    width: 80%;
    display: block;
    margin-bottom: 10px;
  }
  .row-side {
    display: flex;
    flex-direction: column;
  }
}

/* Style the counter card-boxs */
.card-box {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  padding: 30px;
  text-align: center;
  color: var(--color-bg);
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -ms-border-radius: 6px;
  -o-border-radius: 6px;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
  box-shadow: 30px 18px 100% #d1d9e6;
}

.card-box:hover {
  background-color: var(--color-bg);
  border: 1px solid var(--color-primary);
  color: var(--color-white);
}

.card-box .icon {
  font-size: 32px;
  margin-bottom: 10px;
  text-align: center;
}

.card-box h3 {
  margin-bottom: 10px;
}
